@import url("bootstrap/dist/css/bootstrap.css");
@import url("font-awesome/css/font-awesome.css");

.image {
  width: 50px;
  height: 50px;
}

.appjs > .navigation {
  background-color: #086375;

  box-shadow: 0 3px 5px -1px #0003, 0 6px 10px 0 #00000024,
    0 1px 18px 0 #0000001f;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;

  .navbar-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 8px 16px;

    img {
      cursor: pointer;
    }

    .items {
      color: white;
      box-sizing: border-box;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      cursor: pointer;
      outline: none;
      border: none;
      -webkit-tap-highlight-color: transparent;
      display: inline-block;
      white-space: nowrap;
      text-decoration: none;
      vertical-align: baseline;
      text-align: center;
      margin: 0;
      min-width: 64px;
      line-height: 36px;
      padding: 0 16px;
      border-radius: 4px;
      overflow: visible;

      span {
        margin-left: 5px;
      }
    }

    .items:hover {
      background-color: rgba(0, 0, 0, 0.349);
    }
  }
}

.appjs > .react-body {
  margin-top: 80px;
}

.login-container {
  max-width: 400px;
  margin: 0 auto;
  border: 1px solid black;

  .header {
    background-color: lighten($color: #086375, $amount: 2);
    color: white;
    padding: 10px 20px;
    font-weight: bold;
  }
  .body {
    padding: 20px;

    .col-form-label {
      font-weight: 500;
    }
    button.btn {
      background: lighten($color: #086375, $amount: 5);
      color: white;
    }
    button.btn:disabled {
      background: lighten($color: #086375, $amount: 10);
      color: white;
      opacity: 0.75;
    }
    .errors {
      border: 1px solid red;
      background-color: #ffcbcb;
      color: black;
      padding: 5px 10px;
      font-weight: 500;
    }
    .success {
      border: 1px solid green;
      background-color: #afffaf;
      color: black;
      padding: 5px 10px;
      font-weight: 500;
      font-size: 80%;
    }
  }
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;

    div {
      font-size: 12px;

      span {
        cursor: pointer;
        font-weight: 500;
        color: #007bff;
      }

      span:hover {
        text-decoration: underline;
      }
    }
  }
}

//spinner

.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: black;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}

.whole {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 90;
}

.landingPage {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;

  h1 {
    color: #eb3e3e;
  }
}

.rowss {
  cursor: pointer;
}

.tableHeader {
  background: #347e8d;
  color: white;
}
.page > button.btn {
  box-shadow: none;
}

.about {
  text-align: left;
  max-width: 700px;
  padding: 0 20px;
}

.detail {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;

  section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    box-shadow: 0px 0px 11px 0px black;
    margin: 10px;
    border-radius: 5px;
    padding: 10px 5px;

    .image-box {
      box-shadow: 0px 0px 3px 0px black;

      .image {
        width: 170px;
        height: 180px;
      }
    }
    .hori {
      display: block;
      height: 1px;
      border: 0;
      border-top: 1px dashed #000000;
      padding: 0;
      width: 100%;
      margin: 0;
    }
  }

  section:not(:first-child) {
    flex-grow: 1;
    justify-content: space-around;
  }
}
.competi {
  & > div {
    text-align: center;
    margin-bottom: 20px;
    border: 1px dotted black;
    padding: 10px;
    border-radius: 11px;
    box-shadow: 3px 3px 2px 0px #727272;
    cursor: pointer;
    
    &:hover{
      box-shadow: 3px 3px 8px 0px #2a2a2a

    }
  }
  span {
    font-size: 20px;
    font-weight: 600;
  }
  .percent {
    font-size: 15px;
    font-weight: 500;
    color: #4f4f4f;
  }
}
